import React from 'react';
import styled, { createGlobalStyle } from 'styled-components';  // createGlobalStyle import
import { useNavigate } from 'react-router-dom';
import BackgroundImage from '../assets/image/Background1.svg'; // 이미지 파일 import
import ExplaneImage from '../assets/image/explane.svg';  // explane.svg 이미지 import

// @font-face로 폰트 로드
const GlobalStyle = createGlobalStyle`
  @font-face {
    font-family: 'NotoSansKR';
    src: url('./assets/fonts/NotoSansKR-Regular.woff2') format('woff2'),
         url('./assets/fonts/NotoSansKR-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }

  @font-face {
    font-family: 'NanumSquareRound';
    src: url('./assets/fonts/NanumSquareRound-Regular.woff2') format('woff2'),
         url('./assets/fonts/NanumSquareRound-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }
`;

const MainContainer = styled.div`
  font-family: 'NotoSansKR', sans-serif;  /* NotoSansKR 폰트를 기본 폰트로 설정 */
  text-align: center;
  background-color: #FFFFFF;  /* 배경색을 #FFFFFF로 변경 */
  background-image: url(${BackgroundImage});
  background-size: auto 692px;
  background-position: top;
  background-repeat: no-repeat;
  min-height: 100vh;
  position: relative; /* 부모 요소의 위치 설정 */
`;

const ContentContainer = styled.div`
  padding-top: 692px;  /* 배경 이미지 위로 올리기 */
  font-family: 'Arial', sans-serif;
  text-align: center;
`;

const Button = styled.button`
  background-color: #FFFFFF;
  color: #007B8F;
  padding: 15px 30px;
  margin: 20px 0px;
  border: none;
  border-radius: 26px;
  font-size: 18px;
  cursor: pointer;
  font-family: 'NanumSquareRound', sans-serif; /* 버튼 텍스트에 NanumSquareRound 폰트 적용 */
  position: absolute; /* 버튼을 절대 위치로 설정 */
  top: 350px;  /* 숫자 변경하지 마시오 */
  left: 268px; /* 숫자 변경하지 마시오 */
  
  &:hover {
    background-color: #007B8F;
    color: #FFFFFF;
  }

  @media (max-width: 1024px) {
    top: 300px; /* 태블릿에서 위치 조정 */
    left: 50%;
    transform: translateX(-50%);
  }

  @media (max-width: 767px) {
    top: 250px; /* 모바일에서 위치 조정 */
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
    padding: 12px 25px;
  }
`;

const Button2 = styled.button`
  background-color: #007B8F;
  color: #FFFFFF;
  padding: 15px 30px;
  margin: 20px 10px;
  border: none;
  border-radius: 26px;
  font-size: 18px;
  cursor: pointer;
  font-family: 'NanumSquareRound', sans-serif; /* 버튼 텍스트에 NanumSquareRound 폰트 적용 */
  position: absolute; /* 숫자 변경하지 마시오 */
  top: 350px;  /* 숫자 변경하지 마시오 */
  left: 490px;

  &:hover {
    background-color: #FFFFFF;
    color: #007B8F;
  }

  @media (max-width: 1024px) {
    top: 300px; /* 태블릿에서 위치 조정 */
    left: 50%;
    transform: translateX(-50%);
  }

  @media (max-width: 767px) {
    top: 250px; /* 모바일에서 위치 조정 */
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
    padding: 12px 25px;
  }
`;

const ExplaneImageContainer = styled.div`
  position: absolute;
  top: 692px;  /* 배경 이미지 바로 아래로 위치 */
  left: 50%;
  transform: translateX(-50%); /* 이미지가 화면 중앙에 위치하도록 설정 */
`;

function MainPage() {
  const navigate = useNavigate();

  const goToWebtoonEditPage = () => navigate('/webtoon-edit');
  const goToWebtoonFilePage = () => navigate('/webtoon-file');

  return (
    <>
      <GlobalStyle />
      <MainContainer>
        {/* 콘텐츠 영역: 배경 이미지 바로 아래 */}
        <ContentContainer>
          <Button onClick={goToWebtoonEditPage}>작업 공간 바로 가기</Button>
          <Button2 onClick={goToWebtoonFilePage}>새 프로젝트 만들기</Button2>
        </ContentContainer>

        {/* explane.svg 이미지 추가 */}
        <ExplaneImageContainer>
          <img src={ExplaneImage} alt="Explane" />
        </ExplaneImageContainer>
      </MainContainer>
    </>
  );
}

export default MainPage;
