import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';

// 전체 페이지 레이아웃
const PageContainer = styled.div`
  display: flex;
  height: 100vh;
`;

const Sidebar = styled.div`
  width: 300px;
  background-color: #f5f5f5;
  padding: 20px;
  box-sizing: border-box;
`;

const ContentArea = styled.div`
  flex-grow: 1;
  padding: 40px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
`;

const SectionTitle = styled.h2`
  font-size: 20px;
  margin-bottom: 16px;
`;

const FolderList = styled.ul`
  list-style: none;
  padding: 0;
`;

const FolderItem = styled.li`
  margin-bottom: 8px;
  padding: 10px;
  background-color: ${({ selected }) => (selected ? '#1e88e5' : '#ffffff')};
  color: ${({ selected }) => (selected ? 'white' : 'black')};
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  font-weight: ${({ selected }) => (selected ? 'bold' : 'normal')};
`;

const EpisodeList = styled.ul`
  list-style: none;
  padding: 0;
  margin-top: 20px;
`;

const EpisodeItem = styled.li`
  margin-bottom: 6px;
  padding: 8px;
  background-color: ${({ selected }) => (selected ? '#ff9800' : '#ffffff')};
  color: ${({ selected }) => (selected ? 'white' : 'black')};
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  font-weight: ${({ selected }) => (selected ? 'bold' : 'normal')};
`;

const AddButton = styled.button`
  margin-top: 20px;
  padding: 10px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;

  &:hover {
    background-color: #0056b3;
  }
`;

const WebtoonFilePage = () => {
  const navigate = useNavigate();
  const [seriesList, setSeriesList] = useState([]);
  const [selectedSeriesId, setSelectedSeriesId] = useState(null);
  const [selectedEpisodeId, setSelectedEpisodeId] = useState(null);

  useEffect(() => {
    fetchWebtoons();
  }, []);

  const fetchWebtoons = async () => {
    try {
      const response = await fetch("/api/webtoons");
      const data = await response.json();
      setSeriesList(data);

      if (data.length > 0) {
        setSelectedSeriesId(data[0].id || data[0].title);
        if (data[0].episodes?.length > 0) {
          setSelectedEpisodeId(data[0].episodes[0].id || data[0].episodes[0].title);
        }
      }
    } catch (err) {
      console.error("웹툰 목록 불러오기 실패:", err);
    }
  };

  const handleSelectSeries = (seriesId) => {
    setSelectedSeriesId(seriesId);
    const currentSeries = seriesList.find(s => s.id === seriesId || s.title === seriesId);
    if (currentSeries && currentSeries.episodes?.length > 0) {
      setSelectedEpisodeId(currentSeries.episodes[0].id || currentSeries.episodes[0].title);
    } else {
      setSelectedEpisodeId(null);
    }
  };

  const handleSelectEpisode = (episodeId) => {
    setSelectedEpisodeId(episodeId);
    const series = seriesList.find(s => s.id === selectedSeriesId || s.title === selectedSeriesId);
    const episode = series?.episodes.find(e => e.id === episodeId || e.title === episodeId);
    if (series && episode) {
      navigate(`/edit/${series.title}/${episode.title}`);
    }
  };

  const handleAddNewFolder = async () => {
    const folderName = prompt("새 웹툰 이름을 입력하세요");
    if (!folderName) {
      alert("웹툰 이름을 입력해야 합니다.");
      return;
    }

    try {
      const response = await fetch("/api/webtoons", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ title: folderName })
      });

      if (!response.ok) throw new Error("웹툰 생성 실패");
      alert("웹툰 폴더가 생성되었습니다.");
      await fetchWebtoons();
    } catch (err) {
      console.error("웹툰 생성 오류:", err);
      alert("웹툰 생성 실패");
    }
  };

  // ✅ 이미지 업로드 제거 → 디렉토리만 생성
  const handleAddEpisode = async () => {
    const selectedSeries = seriesList.find(s => s.id === selectedSeriesId || s.title === selectedSeriesId);
    if (!selectedSeries) return alert("웹툰을 먼저 선택해주세요.");

    const chapterTitle = prompt("회차 제목을 입력하세요 (예: 1화)");
    if (!chapterTitle) return;

    try {
      const response = await fetch(`/api/webtoons/${selectedSeries.title}/chapters/${chapterTitle}/create`, {
        method: "POST"
      });

      if (!response.ok) throw new Error("회차 생성 실패");
      alert("회차가 생성되었습니다.");
      fetchWebtoons();
    } catch (err) {
      console.error("회차 생성 오류:", err);
      alert("회차 생성 실패");
    }
  };

  return (
    <PageContainer>
      <Sidebar>
        <SectionTitle>웹툰 목록</SectionTitle>
        <FolderList>
          {seriesList.map((series) => (
            <FolderItem
              key={series.id || series.title}
              selected={series.id === selectedSeriesId || series.title === selectedSeriesId}
              onClick={() => handleSelectSeries(series.id || series.title)}
            >
              {series.title}
            </FolderItem>
          ))}
        </FolderList>
        <AddButton onClick={handleAddNewFolder}>웹툰 추가</AddButton>
      </Sidebar>

      <ContentArea>
        <SectionTitle>회차 목록</SectionTitle>
        <EpisodeList>
          {seriesList
            .find((series) => series.id === selectedSeriesId || series.title === selectedSeriesId)
            ?.episodes?.map((episode) => (
              <EpisodeItem
                key={episode.id || episode.title}
                selected={episode.id === selectedEpisodeId || episode.title === selectedEpisodeId}
                onClick={() => handleSelectEpisode(episode.id || episode.title)}
              >
                {episode.title}
              </EpisodeItem>
            ))}
        </EpisodeList>
        {selectedSeriesId && (
          <AddButton onClick={handleAddEpisode}>회차 추가</AddButton>
        )}
      </ContentArea>
    </PageContainer>
  );
};

export default WebtoonFilePage;