import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import MainPage from './pages/MainPage';
import WebtoonFilePage from './pages/WebtoonFilePage';
import WebtoonEditPage from './pages/WebtoonEditPage';
import { Header } from './components/Header';

function App() {
  return (
    <Router>
      <Header />
      <Routes>
        <Route path="/" element={<MainPage />} />
        <Route path="/webtoon-file" element={<WebtoonFilePage />} /> {/* ✅ 기준 경로로 설정 */}
        <Route path="/edit/:title/:chapter" element={<WebtoonEditPage />} />
      </Routes>
    </Router>
  );
}

export default App;
