import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useParams } from "react-router-dom";

const sanitize = (str) =>
  str.replace(/\s+/g, "_").replace(/[^\w가-힣_-]/g, "_");

function WebtoonEditPage() {
  const { title, chapter } = useParams();
  const [fileList, setFileList] = useState([]);
  const [selectedFile, setSelectedFile] = useState(null);
  const [uploading, setUploading] = useState(false);
  const [selectedFiles, setSelectedFiles] = useState({});

  const safeTitle = sanitize(title);
  const safeChapter = sanitize(chapter);

  const handleImageClick = (fileName) => {
    const encodedFile = encodeURIComponent(fileName);
    setSelectedFile(`/uploads/${safeTitle}/${safeChapter}/${encodedFile}`);
  };

  const toggleFileSelection = (fileName) => {
    setSelectedFiles((prev) => ({
      ...prev,
      [fileName]: !prev[fileName],
    }));
  };

  const handleDownload = async () => {
    for (const fileName of Object.keys(selectedFiles)) {
      if (selectedFiles[fileName]) {
        const encoded = encodeURIComponent(fileName);
        const url = `/uploads/${safeTitle}/${safeChapter}/${encoded}`;
        const a = document.createElement("a");
        a.href = url;
        a.download = fileName;
        a.click();
      }
    }
  };

  const fetchFiles = async () => {
    try {
      const response = await fetch(
        `/api/webtoons/${safeTitle}/${safeChapter}/images`
      );
      const data = await response.json();
      setFileList(data);

      if (data.length > 0) {
        const encoded = encodeURIComponent(data[0]);
        const filePath = `/uploads/${safeTitle}/${safeChapter}/${encoded}`;
        setSelectedFile(filePath);
      }
    } catch (err) {
      console.error("이미지 목록 불러오기 실패:", err);
    }
  };

  const handleUpload = async (e) => {
    const files = e.target.files;
    if (!files || files.length === 0) return;

    const formData = new FormData();
    for (const file of files) {
      formData.append("files", file);
    }

    try {
      setUploading(true);
      const response = await fetch(
        `/api/webtoons/${safeTitle}/chapters/${safeChapter}/upload`,
        {
          method: "POST",
          body: formData,
        }
      );
      if (!response.ok) throw new Error("업로드 실패");

      await fetchFiles();
    } catch (err) {
      console.error("업로드 중 오류:", err);
      alert("이미지 업로드 실패");
    } finally {
      setUploading(false);
    }
  };

  useEffect(() => {
    fetchFiles();
  }, [title, chapter]);

  return (
    <>
      <Header><div /><div /></Header>
      <Container>
        <Sidebar>
          <FileList>
            {fileList.map((fileName, idx) => (
              <FileItem key={idx}>
                <input
                  type="checkbox"
                  checked={!!selectedFiles[fileName]}
                  onChange={() => toggleFileSelection(fileName)}
                />
                <span onClick={() => handleImageClick(fileName)}>
                  {fileName}
                </span>
              </FileItem>
            ))}
          </FileList>

          <Button onClick={handleDownload}>⬇ 파일 다운로드</Button>

          <UploadLabel>
            📤 이미지 업로드
            <UploadInput
              type="file"
              multiple
              onChange={handleUpload}
              disabled={uploading}
            />
          </UploadLabel>
        </Sidebar>

        <Main>
          {selectedFile && (
            <ImageViewer src={selectedFile} alt="선택된 이미지" />
          )}
        </Main>
      </Container>
    </>
  );
}

export default WebtoonEditPage;

const Header = styled.div`
  height: 60px;
  background-color: white;
  border-bottom: 1px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  z-index: 3;
  position: relative;
`;

const Container = styled.div`
  display: flex;
  height: calc(100vh - 60px);
  position: relative;
`;

const Sidebar = styled.div`
  width: 250px;
  background-color: #f7f7f7;
  border-right: 1px solid #ddd;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
  position: relative;
`;

const FileList = styled.div`
  overflow-y: auto;
  flex-grow: 1;
`;

const FileItem = styled.label`
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 10px;
  margin-bottom: 6px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  &:hover {
    background-color: #e6f0ff;
    border-color: #007bff;
  }
`;

const Button = styled.button`
  background-color: #28a745;
  color: white;
  padding: 8px 10px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-top: 8px;
`;

const UploadLabel = styled.label`
  display: block;
  margin-top: 10px;
  padding: 10px;
  background-color: #007bff;
  color: white;
  text-align: center;
  border-radius: 8px;
  cursor: pointer;
`;

const UploadInput = styled.input`
  display: none;
`;

const Main = styled.div`
  flex-grow: 1;
  padding: 20px;
  overflow-y: auto;
  z-index: 1;
  background-color: #ffffff;
`;

const ImageViewer = styled.img`
  width: 100%;
  max-height: 100%;
  object-fit: contain;
`;
